import React from "react";
import { Menu, Icon } from 'antd';
import { Link } from "react-router-dom";

/**
 * 
 * @param {*} props defaultSelectedKeys[home, backback, tshirt, hat]
 */
export default function Navbar(props) {
    const backpackStyle = {
        height: "15px",
        width: "15px",
        margin: "0 0 8px"
    };

    const tshirtStyle = {
        height: "18px",
        width: "18px",
        margin: "0 0 8px 1px"
    };

    const hatStyle = {
        height: "18px",
        width: "18px",
        margin: "0 0 8px"
    };

    function isOnDesktop() {
        let width = document.documentElement.clientWidth;
        return width > 500;
    }

    return (
        <Menu
            defaultSelectedKeys={props.defaultSelectedKeys}
            mode={isOnDesktop() ? "horizontal" : "vertical"}
        >
            <Menu.Item key="home">
                <Link to="/">
                    <Icon type="home" />
                    Trang chủ
                </Link>
            </Menu.Item>
            <Menu.Item key="backpack">
                <Link to="/backpack">
                    <Icon component={() => (<img alt="backpack logo" style={backpackStyle} src="/backpack.ico" />)} />
                    Balo
                </Link>
            </Menu.Item>
            <Menu.Item key="tshirt">
                <Link to="/tshirt">
                    <Icon component={() => (<img alt="tshirt logo" style={tshirtStyle} src="/tshirt.ico" />)} />
                    Áo thun
                </Link>
            </Menu.Item>
            <Menu.Item key="hat">
                <Link to="/hat">
                    <Icon component={() => (<img alt="hat logo" style={hatStyle} src="/hat.ico" />)} />
                    Nón
                </Link>
            </Menu.Item>
        </Menu>
    );
}